
.myDrag {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9000;
  background: #fff;
}
.myDrag .dragTitle {
  width: 100%;
  height: 30px;
  background-color: #467cd4;
  float: left;
  cursor: move;
}
.myDrag .dragTitle > a {
  width: 20px;
  height: 30px;
  line-height: 30px;
  float: right;
  display: block;
  margin: 0 5px;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
}
.myDrag .dragTitle > a:hover {
  background-color: #5989d8;
}
.myDrag .dragTitle .minimize > img {
  margin-bottom: 5px;
}
.myDrag .dragContent {
  position: relative;
  float: left;
}
.myDrag .dragContent .myIframe {
  border: none !important;
}
.myDrag .dragContent .dragCon-mask {
  width: 100%;
  height: 100%;
  background: #fff;
  opacity: 0;
  filter: Alpha(0);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 8000;
}
.drag-mask {
  width: 100%;
  height: 100%;
  background: #fff;
  opacity: 0;
  filter: Alpha(0);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 7000;
}


