
.myDesktop {
  position: relative;
  overflow: hidden;
  background-image: url("/static/img/background.png");
  background-size: cover;
  background-position: 0 0;
  background-repeat: no-repeat;
}
.myDesktop .iconList {
  height: 100%;
}
.myDesktop .iconList > ul {
  height: 100%;
  position: relative;
}
.myDesktop .iconList .active {
  background-color: rgba(255,255,255,0.5);
  border: 1px solid #fff;
}
.myDesktop .iconList > ul.iconSize > li {
  width: 68px;
  height: 92px;
}
.myDesktop .iconList > ul > li {
  list-style: none;
  overflow: hidden;
  cursor: pointer;
  text-align: center;
  position: absolute;
  transition: 0.3s all ease-in-out;
  border: 1px solid transparent;
}
.myDesktop .iconList > ul > li:hover {
  background-color: rgba(255,255,255,0.5);
  border: 1px solid #fff;
}
.myDesktop .iconList > ul > li > div {
  width: 70px;
  height: 50px;
}
.myDesktop .iconList > ul > li > span {
  width: 60px;
  display: block;
  margin-left: 5px;
  font-size: 12px;
  text-align: center;
  color: #fff;
  margin-top: 10px;
}
.myDesktop .operation {
  width: auto;
  height: 40px;
  position: fixed;
  bottom: 30px;
  z-index: 100;
  left: 0;
  font-size: 0;
  border-left: 1px solid rgba(255,255,255,0.2);
}
.myDesktop .operation > span {
  display: block;
  float: left;
  width: 94px;
  height: 38px;
  line-height: 38px;
  text-align: center;
  border: 1px solid rgba(255,255,255,0.2);
  border-left: 0 !important;
  font-size: 14px;
  color: rgba(255,255,255,0.2);
  cursor: pointer;
}
.myDesktop .operation > span:hover:before {
  content: '';
}
.myDesktop .operation .line:before {
  content: '纵向';
  background: none;
}
.myDesktop .operation .line:hover {
  width: 94px;
  height: 38px;
  background: url("/static/img/line.png") 0 0 no-repeat;
}
.myDesktop .operation .column:hover {
  width: 94px;
  height: 38px;
  background: url("/static/img/column.png") 0 0 no-repeat;
}
.myDesktop .operation .column:before {
  content: '横向';
  background: none;
}
.myDesktop .operation .close:before {
  content: '关闭';
}
.myDesktop .operation .close:hover {
  background: url("/static/img/colse.png") 0 0 no-repeat;
}


